<template>
	<view class="imagesContentBor">
		<view class="imagesContents">
			<messageUpload :optionsDatas="optionsDatas" :isActive='true'></messageUpload>
		</view>

		<view class="loginButtom">
			<button type="primary" @click="subMit" :disabled="disabled">确定<text v-if="imgList.length>0">
					({{imgList.length}})</text></button>
		</view>
		<view class="">
			<upLoadImages  :optionsDatas="optionsDatas"  @getIdFunc="getIdFunc"></upLoadImages>
		</view>
		<!-- <view class="cu-bar bg-white margin-top">
			<view class="action">
				图片上传
			</view>
			<view class="action">
				{{imgList.length}}/4
			</view>
		</view>
		<view class="cu-form-group">
			<view class="grid col-4 grid-square flex-sub">
				<view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage"
					:data-url="imgList[index]">
					<image :src="imgList[index]" mode="aspectFill"></image>
					<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
						<text class='cuIcon-close'></text>
					</view>
				</view>
				<view class="solids" @tap="ChooseImage" v-if="imgList.length<4">
				1	<text class='cuIcon-cameraadd'></text>
				</view>
			</view>
		</view> -->
	</view>
</template>


<script>
	import { filebindCardh } from "@/api/index.js";
	import imagesList from '@/components/imagesList'
	import messageUpload from '@/components/message/messageUpload.vue'
	import upLoadImages from '@/components/message/upLoadImages.vue'
  
	export default {
		components: {
			imagesList,
			messageUpload,
			upLoadImages
		},
		data() {
			return {
				disabled: true,
				imgList: [],
                optionsData:null,
				optionsDatas:null,
				fileIdArray:[]
			};
		},

		methods: {
			getIdFunc(id){
			 this.fileIdArray.push(id)	
			},
		 
			subMit() {},
			ChooseImage() {
				var that = this
				console.log("this.$upLoad", this.$circle)
				this.$upLoad.uploadImages(5, res => {
					if (!that.imgList) {
						that.imgList = []
					}
					console.log("上传的图片", res)
					that.imgList = that.imgList.concat(res)
					// that.$common.successToShow("上传成功")
				})
			},

			ChooseImage2() {
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: (res) => {
						if (this.imgList.length != 0) {
							this.imgList = this.imgList.concat(res.tempFilePaths)
						} else {
							this.imgList = res.tempFilePaths
						}
					}
				});
			},
			//浏览大图
			ViewImage(e) {
				uni.previewImage({
					urls: this.imgList,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '召唤师',
					content: '确定要删除这段回忆吗？',
					cancelText: '再看看',
					confirmText: '再见',
					success: res => {
						if (res.confirm) {
							this.imgList.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},

		},
		onLoad(options) {
			let optionsData = JSON.parse(options.item)
			this.optionsDatas =optionsData
			console.log("-------》",optionsData)
			// this.getfilebindCardh(optionsData)
		}
	};
</script>

<style scoped>
	.imagesContentBor {
		background: linear-gradient(0deg, #FFFFFF 0%, #FFFFFF 100%);
		min-height: 100vh;
	}

	.imagesContents {
		padding: 40upx 20upx;
		padding-left: 30upx;
	}

	.loginButtom {
		height: 100upx;
		padding: 10px 0;
		background-color: #fff;
		margin-top: 40px;
		position: fixed;
		bottom: 0;
		left: 0;
		right: 0;
	}

	.loginButtom button {
		width: 620upx;
		height: 80upx;
		line-height: 80upx;
		text-align: center;
		border-radius: 100upx;
		font-size: 30upx;
		font-family: PingFang SC;
		font-weight: 400;
	}
</style>
